@import url(https://fonts.googleapis.com/css?family=Libre+Franklin&display=swap);
html body {
  margin: 0;
  padding: 0;
  font-family: 'Libre Franklin', sans-serif;
  color: grey;
  font-size: 1.2em; }

a {
  font-size: 1.3em;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #808080; }

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  margin-bottom: 20px; }

.row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center; }

.iconMenu {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  margin: 10px 20px;
  width: 45px;
  height: 45px;
  background-image: url(data:image/png;base64,);
  z-index: 101; }

.centerNav {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center; }

nav {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  padding: 10px 15px;
  position: fixed;
  top: 0;
  background-color: white;
  align-self: center;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  border-radius: 20px;
  z-index: 100; }
  nav .navlink {
    padding: 0 5px; }
  nav .navlink:hover {
    text-decoration: underline; }

.signature {
  margin-top: 75px;
  width: 350px;
  align-self: center; }

@media screen and (max-width: 500px) {
  nav {
    display: none;
    flex-direction: column;
    width: 100%;
    padding: 10px 15px;
    margin-top: 35px;
    border-radius: 20px; }
    nav .navlink {
      padding: 10px 0; }
  #show {
    display: flex; }
  .iconMenu {
    display: flex; }
  .signature {
    margin-top: 50px;
    width: 70%; } }

.column {
  display: flex;
  justify-content: center;
  align-content: center;
  margin: 30px 10px;
  width: 45%;
  max-width: 460px;
  max-height: 345px;
  border-radius: 20px; }

.thumbnail {
  width: 100%;
  height: 100%; }

a:hover {
  text-decoration: underline; }
  a:hover .thumbnail:hover {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }

.title {
  font-size: 1em;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
  z-index: 999;
  margin: 5px 0 0 10px;
  text-transform: uppercase;
  text-decoration: none;
  color: grey; }

#code01,
#code02,
#code03,
#code04 {
  order: 1; }

#design01,
#design02,
#design03 {
  order: 2; }

#art01,
#art02,
#art03,
#art04 {
  order: 3;
  align-self: center;
  width: 100%;
  max-width: none;
  height: auto;
  max-height: none;
  border-radius: 0; }

#design01 {
  justify-content: center;
  width: 90%;
  max-width: none;
  max-height: none;
  height: auto; }

#design02,
#design03 {
  width: 45%; }

@media screen and (max-width: 500px) {
  .column {
    width: 90%; }
  #code01,
  #code02,
  #code03,
  #code04 {
    order: 1;
    width: 90%;
    max-height: auto; }
  #design01,
  #design02,
  #design03 {
    order: 2;
    width: 90%;
    max-height: auto; } }

@media screen and (min-width: 768px) {
  #code01,
  #code02,
  #code03,
  #code04 {
    width: 40%; } }

.about {
  width: 100%;
  margin: auto;
  max-width: 768px;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.cover {
  width: 90%;
  height: 400px;
  align-self: center; }
  .cover img {
    width: 100%;
    height: 100%;
    border-radius: 10px; }

.info {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-self: center;
  color: grey; }

.abouttitle {
  font-weight: 700;
  padding-top: 10px; }

.bio,
.skills {
  align-self: flex-start;
  width: 100%;
  padding: 10px; }

.skills {
  order: -1;
  text-align: right; }

.feats {
  font-size: 1em;
  text-transform: none; }

.social {
  margin: 10px 10px 0 0;
  width: 30px;
  height: 30px; }

@media screen and (max-width: 500px) {
  .about {
    width: 90%; }
  .cover {
    width: 100%;
    height: auto; }
  .info {
    flex-direction: column;
    width: 90%; }
  .bio,
  .skills {
    align-self: flex-start;
    text-align: left;
    width: 100%;
    padding: 0; } }

.casestudy {
  width: 100%;
  max-width: 768px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin: auto; }

.text-container {
  width: 80%; }

.casestudy-info {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%; }

.tools,
.timeframe {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  text-align: center;
  width: 45%; }

.casestudy-title {
  margin: 20px 0 10px 0; }

.casestudy-img {
  width: 100%; }

.imageGroup {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%; }

#mp03,
#mp04,
#mp05 {
  margin: 10px;
  width: 45%; }

#ksAnimatic {
  margin: 10px;
  width: 45%; }

#ksDesign {
  margin: 10px;
  width: 90%; }

#ks07 {
  width: 60%; }

.video-container {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden; }

.video-container {
  align-self: center;
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden; }

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

@media screen and (max-width: 500px) {
  #mp03,
  #mp04,
  #mp05 {
    width: 70%; } }

